.home-page {
    width: 100%;
    height: 100%;
    padding: 50px 100px 50px 100px;
    background: linear-gradient(to right, rgb(14, 10, 20), rgb(27, 12, 37), rgb(17, 6, 22));
    color: rgb(209, 205, 205);
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.home-page::after {
    z-index: 3;
    content: '';
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 250px; /* Adjust the height as needed */
    pointer-events: none; /* Ensure it doesn't interfere with interactions */
    background: linear-gradient(to bottom, rgba(14, 10, 20, 0), rgb(14, 10, 20)); /* Fade out effect */
}

.home-page-start-content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-bottom: 50px;
}

.home-page-greeting {
    font-weight: 400;
    font-size: 40px;
}

.home-page-greeting-hi,
.home-page-greeting-name {
    opacity: 0;
    animation: fadeIn 1s forwards;
}

.home-page-greeting-name {
    animation-delay: 0.5s; /* Delay for the second text */
}

@keyframes fadeIn {
    to {
        opacity: 1;
    }
}

.home-page-text-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 60px;
}

.home-page-aboutme {
    opacity: 0;
    animation: fadeIn 1s forwards;
    animation-delay: 1.5s;
}

.home-page-aboutme-title {
    font-weight: 500;
    color: rgb(119, 117, 117)
}

.home-page-aboutme-content {
    max-width: 500px; 
}

.home-page-description {
    opacity: 0;
    animation: fadeIn 1s forwards;
    animation-delay: 2s;
}

.home-page-description-title {
    font-weight: 500;
    color: rgb(119, 117, 117)
}

.home-page-description-content {
    max-width: 500px; 
}




