.project-list {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: flex-start;
    gap: 10px;
}

.project-column {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.project-item {
    flex: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    opacity: 0;
    animation: fadeIn 3s forwards;
}

@keyframes fadeIn {
    to {
        opacity: 1;
    }
}

.project-item:nth-child(1) {
    animation-delay: 3s;
}

.project-item:nth-child(2) {
    animation-delay: 3.5s;
}

.project-item:nth-child(3) {
    animation-delay: 4s;
}


.project-item:hover {
    transform: scale(1.01);
    transition: transform 0.3s;
}

.project-video,
.project-image {
    max-width: 100%;
    height: auto;
    object-fit: contain;
}

.project-text {
    text-align: left;
}

.project-title {
    font-weight: 500;
    font-size: 16px;
}

.project-description {
    color: rgb(119, 117, 117);
    font-size: 14px;
}

@media (max-width: 768px) {
    .project-list {
        flex-direction: column; /* Stack columns vertically */
    }

    .project-column {
        flex: none; /* Remove equal space distribution */
        width: 100%; /* Make each column take full width */
    }
}